import React from 'react';
import SkyLight from 'react-skylight';
import {ComponentValueTypes} from '../Components/ComponentTypes.js';

class ModifyPortValueView extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            defaultValue: this.props.port.value
        }
    }

    render() {
        return (
            <SkyLight ref={ref => this.skylight = ref} title="Modify Value">
                <p>Type: {this.props.port.getTypeName()}</p>
                <input ref={ref => this.input = ref} onKeyPress={e => this.keypress(e)}/>
                <br /><br />
                <button onClick={() => this.modifyValue()}>Done</button>
            </SkyLight>
        );
    }

    show() {
        this.input.value = this.state.defaultValue;
        this.input.select();
        this.skylight.show();
    }

    modifyValue() {
        let value = this.input.value;
        let valueType = this.props.port.valueType;
        
        if (valueType === ComponentValueTypes.Number) {
            if (isNaN(parseFloat(value))) {
                this.props.port.setValue(null);
            } else {
                this.props.port.setValue(parseFloat(value));
            }
        } else if (valueType === ComponentValueTypes.String) {
            this.props.port.setValue(value);
        }

        this.skylight.setState({isVisible: false});
    }

    keypress(e) {
        if (e.charCode === 13) {
            this.modifyValue();
        }
    }
}

export default ModifyPortValueView;


