import {ComponentPortTypes} from './ComponentTypes.js';

class ComponentPortLink {
    constructor(outPort, inPort) {
        if (outPort.type !== ComponentPortTypes.Out || inPort.type !== ComponentPortTypes.In || outPort.valueType !== inPort.valueType) {
            console.log("Invalid port link (" + outPort.name + "<" + outPort.getTypeName() + "> to " + inPort.name + "<" + outPort.getTypeName() + ">)");
            this.valid = false;
            return;
        }

        this.outPort = outPort;
        this.inPort = inPort;
        this.valid = true;

        this.outPort.addValueChangedListener((port, value) => this.outPortValueChanged(port, value));
    }

    update() {
        this.inPort.setValue(this.outPort.value);
    }

    outPortValueChanged(port, value) {
        this.inPort.setValue(value);
    }
}

export default ComponentPortLink;


